/* GIG PAGE */
.gigWrapper {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 50% 44%;
  grid-template-rows: 60px 1fr 50px;
  grid-template-areas:
    'where when'
    'what what'
    'submit submit';
}
.gigDate {
  grid-area: when;
}
.gigLocation {
  grid-area: where;
}
p.gigLocation {
  margin-left: -1px;
}
.gigWrapper label {
}
.setListWrapper {
  grid-area: what;
  position: relative;
  overflow-y: scroll;
}
.setListWrapper h3 {
  margin: 5px 0px 10px 0px;
}
.setListAddBtn {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 50px;
  height: 50px;
}
.gigSubmit {
  grid-area: submit;
  margin: 0;
}
.gigList {
  width: 100%;
}
.gigList li {
  display: -webkit-flex;
  display: flex;
}
.setListWrapper .gigList button,
.sharksFavWrapper .gigList button {
  -webkit-flex-grow: 0;
          flex-grow: 0;
  width: auto !important;
  margin: 2px !important;
}
.gigList button:first-of-type {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.gigWrapper input {
  width: 100%;
}

/* SHARK */
.sharkWrapper {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 50% 44%;
  grid-template-rows: 70px 1fr 50px;
  grid-template-areas:
  'who when'
  'favs favs'
  'submit submit';
}
.sharkName {
  grid-area: who;
}
.sharkSince {
  grid-area: when;
}
.sharksFavWrapper {
  grid-area: favs;
  position: relative;
  overflow-y: scroll;
}
.sharksFavWrapper h3 {
  margin: 5px 0px 10px 0px;
}
.sharkSubmit {
  grid-area: submit;
  width: 90%;
  margin: 0 auto;

}
