@keyframes rush {
  from {-webkit-transform: scale(0.015) perspective(140px) rotateX(70deg);transform: scale(0.015) perspective(140px) rotateX(70deg); text-shadow: 8px 8px 0px white, 12px 12px 9px crimson;}
  to {-webkit-transform: scale(.97) perspective(150px) rotateX(0deg);transform: scale(.97) perspective(150px) rotateX(0deg); text-shadow: 1px 1px 0px white, 4px 4px 0px crimson;}
}
@-webkit-keyframes shake {
  0% {-webkit-transform: scale(1.01);transform: scale(1.01)}
  100% {-webkit-transform: scale(0.99);transform: scale(0.99)}
}
@keyframes shake {
  0% {-webkit-transform: scale(1.01);transform: scale(1.01)}
  100% {-webkit-transform: scale(0.99);transform: scale(0.99)}
}
@-webkit-keyframes fadeIn {
  0% {opacity: 0}
  100% {opacity: 1}
}
@keyframes fadeIn {
  0% {opacity: 0}
  100% {opacity: 1}
}
@-webkit-keyframes fadeOut {
  from {opacity: 1}
  to {opacity: 0}
}
@keyframes fadeOut {
  from {opacity: 1}
  to {opacity: 0}
}
.fadeOut {
  -webkit-animation: fadeOut .3s ease-out;
          animation: fadeOut .3s ease-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes blink {
  0% {opacity: 0}
  50% {opacity: 1}
}
@keyframes blink {
  0% {opacity: 0}
  50% {opacity: 1}
}
.loaderWrapper {
  -webkit-animation: shake 0.1s 5 linear;
          animation: shake 0.1s 5 linear;
  -webkit-animation-delay: 1.8s;
          animation-delay: 1.8s;
  border: 10px double black;
  background: #e8d6cd;
}

.loadingH1 {
  font-size: 3em;
  color: black;
  text-align: center;
  font-weight: normal;
  text-shadow: 1px 1px 0px white, 4px 4px 0px crimson;
  -webkit-animation: rush 1.7s ease-in-out, shake 0.1s 5 ease-in;
          animation: rush 1.7s ease-in-out, shake 0.1s 5 ease-in;
  -webkit-animation-delay: 0s, 1.7s;
          animation-delay: 0s, 1.7s;
}

.loadingTag {
  font-size: 1.5em;
  opacity: 0;
  -webkit-animation: fadeIn 0.5s ease-in-out;
          animation: fadeIn 0.5s ease-in-out;
  -webkit-animation-delay: 2.2s;
          animation-delay: 2.2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.blink {
  -webkit-animation: blink alternate infinite 0.33s ease-in-out;
          animation: blink alternate infinite 0.33s ease-in-out;
  -webkit-animation-delay: 2.4s;
          animation-delay: 2.4s;
}
.gigDate {
  padding-right: 5px;
}

@-webkit-keyframes grow {
  from {-webkit-transform: scale(0.3);transform: scale(0.3)},
  to {-webkit-transform: scale(1);transform: scale(1)}
}

@keyframes grow {
  from {-webkit-transform: scale(0.3);transform: scale(0.3)},
  to {-webkit-transform: scale(1);transform: scale(1)}
}
.modalBG > * {
  -webkit-animation: grow 0.3s cubic-bezier(.17,.67,.39,1.1);
          animation: grow 0.3s cubic-bezier(.17,.67,.39,1.1);
}
.modalBox {
  -webkit-perspective: 600px;
          perspective: 600px;
}
.modalBoxInner {
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}
[class^="face"] {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.facePasswordSuccess {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.facePasswordSuccess h2 {
  font-family: 'Metal Mania';
  text-align: center;
  font-size: 6rem;
}
.passwordForget {
  font-size: 0.9rem;
  color: #0711a0;
  text-align: center;
  font-weight: bold;
  text-decoration: underline;
  font-family: 'Futura';
  font-style: italic;
  text-transform: uppercase;
}
.passwordForget:disabled {
  opacity: 0.4;
}
.isFlipped {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.modalBox p {
  color: #b00;
  font-weight: bold;
  text-align: center;
}

.greyDisabled {
  color: darkgrey;
  box-shadow: -5px 5px 1px currentColor;
  border-color: currentColor;
}



